* {
  margin: 0;
  padding: 0;
}

html, body, form {
  height: 100%;
}

form input {
  box-sizing: border-box;
  width: 100%;
  max-width: 500px;
  padding: 5px 10px;
}

.create-wrap, .update-wrap {
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  max-width: 800px;
  height: 100%;
  margin: 0 auto;
}

@media screen and (orientation: portrait) {
  .create-wrap, .update-wrap {
    flex-direction: column;
  }
}

.multi-input-wrap {
  gap: 5px;
  width: 100%;
  max-width: 500px;
  display: flex;
}

.create-wrap {
  display: none;
}

.show-create .create-wrap {
  display: flex;
}

.tab-create, .tab-update {
  text-align: center;
  cursor: pointer;
  width: 50%;
  height: 30px;
  line-height: 30px;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.show-create .tab-create, .show-create .tab-update, .show-update .tab-create, .show-update .tab-update {
  display: block;
}

.tab-update {
  left: 50%;
}

.show-update .tab-update, .show-create .tab-create {
  background: #adff2f;
}

.update-wrap {
  display: none;
}

.show-update .update-wrap {
  display: flex;
}

.connect-wrap {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.show-create .connect-wrap, .show-update .connect-wrap {
  display: none;
}

.connect-wrap button {
  color: #0009;
  cursor: pointer;
  background: #adff2f;
  border: none;
  border-radius: 3px;
  padding: 10px 15px;
  font-size: 1.5em;
}

.connect-wrap button:hover {
  color: #000c;
}
